module.exports = "form label {font-weight:bold}\n\n/* .container{\n    height:400px;\n    width:500px;\n    background-color:rgb(252,249,249);\n    //background-color: rgb(255, 253, 253);\n    border-left:1pxsolid;\n    border-top:1pxsolid;\n    padding:10px;\n    box-shadow:5px10px8px#888888;\n    }  */\n\n/* .container{\n    height: 400px;\n    width: 500px;\n    background-color:rgb(252,249,249);\n    border-left:1pxsolid;\n    border-top:1pxsolid;\n    padding:10px;\n    box-shadow:5px10px8px#888888;\n\n} */\n\n.ng-valid[required], .ng-valid.required  {\n    border-left: 5px solid #42A948; \n  }\n\n.ng-invalid:not(form)  {\n    border-left: 5px solid #a94442; \n  }\n\n.ng-untouched:not(form) {\n    border-left: 5px solid rgb(14, 85, 151); \n}\n\n.container{\n    /* height: 550px; */\n    width: 500px;\n    background-color: rgb(252, 249, 249);\n    /* background-color: rgb(255, 253, 253); */\n    border-left: 1px solid;\n    border-top: 1px solid; \n    padding: 10px;\n    box-shadow: 5px 10px 8px #888888;\n}\n\nlabel{\n    font-weight:bold;\n}\n\nh1 {\n    color: #369;\n    font-family: Arial, Helvetica, sans-serif;\n    font-size: 200%;\n}\n\nh2, h3 {\n    color: #444;\n    font-family: Arial, Helvetica, sans-serif;\n    font-weight: lighter;\n}\n\nbutton {\n    font-family: Arial;\n    background-color: #42A948;\n    border: none;\n    padding: 5px 10px;\n    border-radius: 4px;\n    /* cursor: pointer; */\n    cursor: hand;\n}\n\nbutton:hover {\n    background-color: green;\n}\n\nbutton:disabled {\n    background-color: #42A948;\n    color: black;\n    cursor: none;\n}"